@import "../utils/mixins";

$week: 
  "mon" 0,
  "tue" 1,
  "wed" 2,
  "thu" 3,
  "fri" 4,
  "sat" 5,
  "sun" 6;

$day-size: 100px;
$day-number-per-line: 7;
$calendar-margin-top: 100px;
$calendar-padding: 10px;
$calendar__day-transform: -20px;
$calendar__day-transition: 0.5s;
$calendar-border: 1px;
$calendar-gap: 1px;
$calendar-width: ($day-size * $day-number-per-line) + (2 * $calendar-gap)
  + (4 * $calendar-border) + (2 * $calendar-padding);
$first-day-margin: $calendar-width - $day-size - (2 * $calendar-padding);
